html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-sans);
  font-size: 1.125rem;
  color: var(--grayishblue);
  line-height: 1.3;
}
body.noscroll {
  overflow: hidden;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

button,
.button {
  padding: 0.875rem 2.1875rem;
  border-radius: 3.125rem;
  border: 0;
  color: var(--white);
  cursor: pointer;
  background: linear-gradient(to right, var(--limegreen), var(--brightcyan));
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

:root {
  --darkblue: hsl(233, 26%, 24%);
  --limegreen: hsl(136, 65%, 51%);
  --brightcyan: hsl(192, 70%, 51%);
  --grayishblue: hsl(233, 8%, 62%);
  --lightgrayish-blue: hsl(220, 16%, 96%);
  --verylight-gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --button-background: linear-gradient(
    225deg,
    var(--limegreen) 0%,
    var(--brightcyan) 98.02%
  );
}

:root {
  --font-sans: "Public Sans", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 300ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 300ms ease-in-out forwards;
}

.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-image: linear-gradient(var(--darkblue), transparent);
}
.header .container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.header nav {
  position: relative;
  background-color: var(--white);
  padding-top: 1.0625rem;
  padding-bottom: 1.0625rem;
}
.header__logo img {
  width: 8.8125rem;
  height: 1.375rem;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--darkblue);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__menu {
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  background: var(--white);
  padding: 1.625rem;
  margin-top: 1.5rem;
  border-radius: 0.3125rem;
}
.header__menu a {
  color: var(--darkblue);
  padding: 0.625rem;
  display: block;
  text-align: center;
}
.header__links a {
  position: relative;
  font-size: 0.875rem;
  color: var(--grayishblue);
  transition: color 500ms ease-in-out;
}
.header__links a:not(:last-child) {
  margin-right: 2rem;
}
.header__links a::before {
  content: "";
  display: block;
  height: 0.3125rem;
  left: 0;
  right: 0;
  position: absolute;
  bottom: -1.875rem;
  background: var(--button-background);
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}
.header__links a:hover {
  color: var(--darkblue);
}
.header__links a:hover::before {
  opacity: 1;
}
.header__cta {
  font-size: 0.875rem;
  color: var(--white);
  transition: opacity 500ms ease-in-out;
}
.header__cta:hover {
  opacity: 0.75;
}